import React, { useState } from "react";
import { Link } from "react-router-dom";
import Logo from "../assets/images/logo.png";

const Navbar = () => {
	return (
		<nav
			style={{ zIndex: 9999 }}
			className="flex w-full min-h-18 justify-center bg-white bg-opacity-50 fixed backdrop-blur-18 shadow-navbar nav-fallback top-0"
		>
			<NavItems />
		</nav>
	);
};

export default Navbar;

const NavItems = () => {
	const [navbarOpen, setNavbarOpen] = useState(false);
	const toggle = () => setNavbarOpen(!navbarOpen);

	return (
		<div className="flex items-center flex-wrap px-8 sm:p-0 w-full sm:w-5/6 lg:w-4/6">
				<NavBrand>Hack Your Own</NavBrand>
			<NavMenu func={toggle} />
			<div
				className={
					"w-full block lg:space-x-9 lg:flex lg:items-center lg:w-auto" +
					(navbarOpen ? "" : " hidden")
				}
			>
				<NavLink to="/program">Our Program</NavLink>
				<NavLink to="/get-involved">Get Involved</NavLink>
				<NavLink to="/meet-us">Meet Us</NavLink>
				<NavLink to="/login">Login</NavLink>
			</div>
		</div>
	);
};

const NavBrand = ({ children }) => {
	return (
		<div className="flex-grow space-x-4 flex">
			<Link to="/" className="flex items-center flex-shrink-0 mr-6">
				<img className="h-8 w-8 mr-2 lg:h-12 lg:w-12" src={Logo} alt="logo" />
				<span className="font-semibold text-xl tracking-tight">{children}</span>
			</Link>
		</div>
	);
};

const NavLink = ({ to, children }) => {
	return (
		<Link to={to} className="w-full block text-left lg:w-auto">
			{children}
		</Link>
	);
};

const NavMenu = ({ func }) => {
	return (
		<div className="block lg:hidden">
			<button onClick={func} className="flex items-center px-3 py-2">
				<svg
					className="h-5 w-5"
					viewBox="0 0 20 20"
					xmlns="http://www.w3.org/2000/svg"
				>
					<title>Menu</title>
					<path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
				</svg>
			</button>
		</div>
	);
};
